import Axios from "axios";
import React, { Component } from "react";
import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory from "react-bootstrap-table2-paginator";
import ToolkitProvider, { Search } from "react-bootstrap-table2-toolkit";
import Skeleton from "react-loading-skeleton";
import SelectSearch from "react-select-search";
import { getListRekapBonSelected } from "../actions/rekapBonAction";
import { berhasil, gagal } from "../Alert";
import { connect } from "react-redux";
import Stepper from "react-stepper-horizontal/lib/Stepper";
import { getUUID } from "../actions/fakturAction";
import HeaderCard from "../HeaderCard";

const server = process.env.REACT_APP_BACKEND_URL;
const serverNonMaster = process.env.REACT_APP_BACKEND_URL_NON_MASTER;

const maptoState = (state) => {
  return {
    listDataRekapBon: state.provinsi.listDataRekapBon,
    username: state.provinsi.username,
    noFaktur: state.provinsi.noFaktur,
  };
};
const { SearchBar } = Search;
class RekapBonCustomer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      step: 0,
      step1: "row",
      step2: "row d-none",
      step3: "row d-none",
      listCustomer: [],
      listDataRekapBon: [],
      columns: [
        {
          dataField: "tanggal",
          text: "Tanggal",
        },
        {
          dataField: "noBon",
          text: "Nomor Bon",
        },
        {
          dataField: "namaAktor",
          text: "Nama Aktor",
        },
        {
          dataField: "bruto",
          text: "Bruto(Gr)",
        },
        {
          dataField: "netto",
          text: "Murni(Gr)",
          footerClasses: "sum-netto-terima-supplier",
        },
        {
          dataField: "rp",
          text: "Ongkos(RP)",
        },
      ],
      columns2: [
        {
          dataField: "tanggal",
          text: "Tanggal",
        },
        {
          dataField: "noBon",
          text: "Nomor Bon",
        },
        {
          dataField: "namaAktor",
          text: "Nama Aktor",
        },
        {
          dataField: "bruto",
          text: "Bruto(Gr)",
        },
        {
          dataField: "netto",
          text: "Murni(Gr)",
          footerClasses: "sum-netto-terima-supplier",
        },
        {
          dataField: "rp",
          text: "Ongkos(RP)",
        },
      ],
    };
  }
  disabledButton = (e) => {
    e.target.disabled = true;
  };
  onChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };
  getListClose() {
    Axios.get(serverNonMaster + "cst/dc/pjl/heads", {
      params: {
        statusValid: "VALID",
      },
    }).then((res) =>
      this.setState({
        listDataRekapBon: res.data,
      })
    );
  }
  componentDidMount() {
    Axios.get(server + "customers")
      .then((res) =>
        this.setState({
          listCustomer: res.data,
        })
      )
      .catch(() =>
        gagal("Mohon periksa koneksi anda, gagal komunikasi dengan server")
      );
    this.getListClose();
    this.props.dispatch(getUUID());
  }
  nextStep = () => {
    const { step } = this.state;
    if (step === 0) {
      this.setState({ step: step + 1, step2: "row", step1: "row d-none" });
    }
    this.props.dispatch(getListRekapBonSelected());
  };
  prevStep = () => {
    const { step } = this.state;
    if (step === 1) {
      this.setState({ step: step - 1, step2: "row d-none", step1: "row" });
    }
  };
  setGabung(data) {
    var filter = JSON.parse(localStorage.getItem("FakturRekapBon")) || [];
    var sorted = filter.sort(function (a, b) {
      return new Date(a.tanggal) - new Date(b.tanggal);
    });
    var isi = {
      fakturList: filter,
      footerDocument: {
        inputBy: this.props.username,
        statusValid: "VALID",
      },
      noFaktur: this.props.noFaktur,
      tanggal: new Date().toISOString().slice(0, 10),
      noBon: this.props.noFaktur,
      tipeTransaksi: "REKAP-BON",
      keterangan: sorted[0].noFakturPilih,
    };
    console.log(JSON.stringify(isi));
    Axios.post(serverNonMaster + "cst/dc/byr", isi)
      .then(() =>
        berhasil("Penggabungan berhasil", this.props.dispatch(getUUID()))
      )
      .then(() => localStorage.removeItem("FakturRekapBon"))
      .then(() => this.props.dispatch(getUUID()))
      .then(() => this.prevStep())
      .catch(() => gagal("Penggabungan gagal, Mohon periksa koneksi anda"));
  }
  setToko(data) {
    this.setFilterCustomer(data);
    this.setState({
      namaToko: data,
    });
  }
  setFilterCustomer(data) {
    Axios.get(serverNonMaster + "cst/dc/pjl/heads", {
      params: {
        statusValid: "OPEN",
      },
    })
      .then((res) =>
        this.setState({
          listDataRekapBon: res.data,
        })
      )
      .then(() => {
        var array = this.state.listDataRekapBon;
        var filtered = array.filter(
          (list) => list.kodeAktor === data.toUpperCase()
        );
        this.setState({
          listDataRekapBon: filtered,
        });
      });
  }
  setFilterTanggal(data) {
    Axios.get(serverNonMaster + "cst/dc/pjl/heads", {
      params: {
        statusValid: "OPEN",
      },
    })
      .then((res) =>
        this.setState({
          listDataRekapBon: res.data,
        })
      )
      .then(() => {
        var array = this.state.listDataRekapBon;
        var filtered = array.filter((list) => list.tanggal === data);
        this.setState({
          listDataRekapBon: filtered,
        });
      });
  }
  render() {
    const selectRow = {
      mode: "checkbox",
      clickToSelect: true,
      onSelect: (row, isSelect, rowIndex, e) => {
        console.log(row.noFaktur);
        let array = JSON.parse(localStorage.getItem("FakturRekapBon")) || [];
        const data = {
          tanggal: row.tanggal,
          noFakturPilih: row.noFaktur,
          namaAktor: row.namaAktor,
          bruto: row.bruto,
          netto: row.netto,
          rp: row.rp,
        };
        if (isSelect) {
          array.push(data);
          localStorage.setItem("FakturRekapBon", JSON.stringify(array));
        } else {
          var index = array.findIndex((item, i) => {
            return item.noFaktur === row.noFaktur;
          });
          array.splice(index, 1);
          localStorage.setItem("FakturRekapBon", JSON.stringify(array));
        }
      },
      onSelectAll: (isSelect, rows, e) => {
        var array = [];
        rows.forEach(function (list) {
          const data = {
            tanggal: list.tanggal,
            noFakturPilih: list.noFaktur,
            namaAktor: list.namaAktor,
            bruto: list.bruto,
            netto: list.netto,
            rp: list.rp,
          };
          array.push(data);
        });
        if (isSelect) {
          localStorage.setItem("FakturRekapBon", JSON.stringify(array));
        } else {
          localStorage.removeItem("FakturRekapBon");
        }
      },
    };
    return (
      <div className="container-fluid">
        <div className="col-lg-12 col-md-12">
          <div className="card">
            <HeaderCard title="Rekap Bon Customer - Penjualan" />
            <div className="card-body">
              <div className="row m-2">
                <div className="col-lg-12 col-md-12 mb-5">
                  <Stepper
                    steps={[
                      { title: "Pilih Data" },
                      { title: "Rekap Data" },
                      // { title: "Data Rekening" },
                    ]}
                    activeStep={this.state.step}
                  />
                </div>
                <div className="col-lg-12">
                  <div className={this.state.step1}>
                    <div className="col-lg-4 col-md-4">
                      <label htmlFor="">Tanggal</label>
                      <input
                        type="date"
                        name="tanggal"
                        className="form-control"
                        onChange={this.onChange("tanggal")}
                      />
                    </div>
                    <div className="col-lg-4 col-md-4">
                      <label htmlFor="">Nama Toko</label>
                      <SelectSearch
                        options={this.state.listCustomer.map((obj) => {
                          var payload = {
                            value: obj.kodeCustomer,
                            name: obj.namaToko,
                          };
                          return payload;
                        })}
                        search
                        placeholder="Silahkan Pilih"
                        onChange={(data) => this.setToko(data)}
                      />
                    </div>
                    <div className="col-lg-12 mt-5">
                      {this.state.listDataRekapBon ? (
                        <ToolkitProvider
                          keyField="noFaktur"
                          data={this.state.listDataRekapBon}
                          columns={this.state.columns}
                          search
                        >
                          {(props) => (
                            <div className="form-group">
                              <div className="text-right">
                                <SearchBar
                                  {...props.searchProps}
                                  className="form-control"
                                  placeholder="Pencarian"
                                  style={{ margin: 0, top: "50%" }}
                                />
                              </div>
                              <BootstrapTable
                                {...props.baseProps}
                                striped
                                hover
                                condensed
                                bordered={true}
                                headerClasses="header-table"
                                pagination={paginationFactory()}
                                selectRow={selectRow}
                              />
                            </div>
                          )}
                        </ToolkitProvider>
                      ) : (
                        <BootstrapTable
                          keyField="noFaktur"
                          data={[]}
                          columns={this.state.columns}
                          striped
                          hover
                          condensed
                          bordered={true}
                          headerClasses="header-table"
                          noDataIndication="Tidak Ada Data"
                        />
                      )}
                    </div>
                    <div className="col-lg-12">
                      <div className="text-right">
                        <button
                          className="btn btn-primary"
                          onClick={() => this.nextStep()}
                        >
                          Next
                          <i className="fa fa-chevron-right ml-3"></i>
                        </button>
                      </div>
                    </div>
                  </div>
                  <div className={this.state.step2}>
                    <div className="col-lg-12 mt-5">
                      {this.state.listDataRekapBon ? (
                        <ToolkitProvider
                          keyField="noFaktur"
                          data={
                            JSON.parse(
                              localStorage.getItem("FakturRekapBon")
                            ) || []
                          }
                          columns={this.state.columns}
                          search
                        >
                          {(props) => (
                            <div className="form-group">
                              <div className="text-right">
                                <SearchBar
                                  {...props.searchProps}
                                  className="form-control"
                                  placeholder="Pencarian"
                                  style={{ margin: 0, top: "50%" }}
                                />
                              </div>
                              <BootstrapTable
                                {...props.baseProps}
                                striped
                                hover
                                condensed
                                bordered={true}
                                headerClasses="header-table"
                                pagination={paginationFactory()}
                              />
                            </div>
                          )}
                        </ToolkitProvider>
                      ) : (
                        <Skeleton width={"100%"} height={100} />
                      )}
                    </div>
                    <div className="col-lg-12">
                      <div className="text-right">
                        <button
                          className="btn btn-primary"
                          onClick={() => this.setGabung()}
                        >
                          Gabung Bon
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptoState, null)(RekapBonCustomer);
